import React, { useContext } from 'react';
import Container, { ContainerContext } from './container';
import Intro from './scenes/intro';
import Various from './scenes/various';
import Mosaic from './scenes/mosaic';
import Run from './scenes/run';

function Scenes() {
  const { y } = useContext(ContainerContext);
  return (
    <>
      {(y > -2000) && <Intro start={0} />}

      {(y < -1200 && y > -6000) && <Various start={1600} />}

      {(y < -5200 && y > -8600) && <Run start={5600} />}

      {(y < -7600) && <Mosaic start={8000} dur={3000} fadeStart={700} fadeDur={1500} />}
    </>
  );
}

function Matsurisu() {
  return (
    <Container>
      <Scenes />
    </Container>
  ); 
}

export default Matsurisu;
