import React from 'react';
import { PixelZoom, ImageZoom } from '../zoom';
import Fade from '../fade';

export default function Mosaic({ start, dur, fadeStart, fadeDur}) {
  return (
    <>
      <PixelZoom initialZoom={100} zoomStart={start} zoomEnd={start + fadeStart + fadeDur} zoomDur={dur} totalHeight={800} />
      <ImageZoom initialZoom={100} zoomStart={start} zoomDur={dur} fadeStart={start + fadeStart} fadeDur={fadeDur} totalHeight={800} />
      <Fade start={start + dur + 300} end={start + dur + 1300} layer={200}>
        <img alt="love" src="/matsurisu/static/img/mosaic.png" />
      </Fade>
      <Fade start={start + dur + 300} end={start + dur + 1300} layer={500} offset={320}>
        <p className="title-congrats">50万人おめでとう！！！</p>
      </Fade>
    </>
  );
}
